
* {
    box-sizing: border-box;
    border-radius: 10px;
    outline: none;
    border: none;
    margin: 0px;
    padding: 0px;
    font-family: Courier, monospace;
}

 body {
        background-image: url(https://static.tumblr.com/maopbtg/nBUmgtogx/paper.png);
            background-repeat:repeat;
} 

.paper {
    color: #FFF;
    font-size: 20px;
        display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;

}

.margin {
    margin-left: 12px;
    margin-bottom: 20px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}

.text {
     word-wrap: break-word;
    resize: none;
    height: 400px;
    width: 450px;
    overflow: auto;
    background-color: #FFF;
    color: #222;
    font-family: Courier, monospace;
    font-weight: normal;
    font-size: 24px;
    resize: none;
    line-height: 40px;
    padding-left: 100px;
    padding-right: 100px;
    padding-top: 45px;
    padding-bottom: 34px;
    background-image: url(https://static.tumblr.com/maopbtg/E9Bmgtoht/lines.png), url(https://static.tumblr.com/maopbtg/nBUmgtogx/paper.png);
    background-repeat: repeat-y, repeat;
    -webkit-border-radius: 12px;
    border-radius: 12px;
    -webkit-box-shadow: 0px 2px 14px #000;
    box-shadow: 0px 2px 14px #000;
    border-top: 1px solid #FFF;
    border-bottom: 1px solid #FFF;
}

.title {
    background-color: transparent;
    border-bottom: 3px solid #FFF;
    color: #FFF;
    font-size: 20px;
    font-family: Courier, monospace;
    height: 28px;
    font-weight: bold;
    width:300px;
    margin:0 0 15px 0;
}

.button {
    cursor: pointer;
    margin-top: 20px;
    float: right;
    height: 40px;
    padding-left: 24px;
    padding-right: 24px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 20px;
    color: #FFF;
    text-shadow: 0px -1px 0px #000000;
    -webkit-border-radius: 8px;
    border-radius: 8px;
    border-top: 1px solid #FFF;
    -webkit-box-shadow: 0px 2px 14px #000;
    box-shadow: 0px 2px 14px #000;
    background-color: #62add6;
    background-image: url(https://static.tumblr.com/maopbtg/ZHLmgtok7/button.png);
    background-repeat: repeat-x;
}

.button:active {
    zoom: 1;
    filter: alpha(opacity=80);
    opacity: 0.8;
}

.button:focus {
    zoom: 1;
    filter: alpha(opacity=80);
    opacity: 0.8;
}

.wrapper {
    padding: 20px 0 0 0;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;

}
@media (max-width:768px) {
    .title {
        width: 200px;
    }
    .text  {
        width: 380px;
            padding-left: 40px;
                padding-right: 40px;
                padding-top: 10px;
                padding-bottom: 10px;
    }
}
@media (max-width:488px) {
    .title {
        width: 200px;
    }
    .text  {
        width: 280px;
        height: 300px;
            padding-left: 40px;
                padding-right: 40px;
                padding-top: 10px;
                padding-bottom: 10px;
    }
}